            flat
          />

          <v-text-field
            color="info"
            label="Email"
            solo
            flat
          />

          <v-text-field
            color="info"
            label="Subject"
            solo
            flat
          />

          <v-textarea
            color="info"
            label="Message"
            solo
            flat
          />

          <base-btn>Send</base-btn>
        </v-sheet>
      </v-col>
