  </section>
  </div>


















      <div  id="fac"><VueFaqAccordion :items="myItems" /></div>
    <core-footer />
  </v-app>
</template>

<script>
import axios from 'axios';
import VueFaqAccordion from "vue-faq-accordion";
import MainPlayer from './../components/PlayerMusic/components/MainPlayer';
  export default {
    name: 'App',
    components: {
      CoreFooter: () => import('@/components/core/Footer'),
      VueFaqAccordion,
      MainPlayer
    },
      props: {
    id: {
      type: String,
      default: "no",
    },
  },
      data() {
    return {
      carreras:{nombre:'',imagen:''},
      myItems:[],
      career: String,
                  skills: [
        {
          name: 'Web Design',
          value: 100,
        },
        {
          name: 'Web Development',
          value: 75,
        },
        {
          name: 'Web Support',
          value: 90,
        },
      ],
      projects: [
        'project1',
        'project2',
        'project3',
        'project4',
      ],
            services: [
        {
          name: 'Research',
          icon: 'mdi-clipboard-text-outline',
          blurb: 'Curabitur et nisi semper,<br> pellent e sque ',
        },
        {
          name: 'Design',
          icon: 'mdi-pencil-outline',
          blurb: 'Curabitur et nisi semper, <br>pellent.',
        },
        {
          name: 'Development',
          icon: 'mdi-settings-outline',
          blurb: 'Curabitur et nisi semper, <br>pellent.',
        },
        {
          name: 'Support',
          icon: 'mdi-account',
          blurb: 'Curabitur et nisi semper, <br>pellent.',
        },
      ],
    };
  },



    mounted() {
    this.freqQuestion()
  },
  methods: {
          freqQuestion(){
        
        this.carreras.nombre=localStorage.getItem("carrer_name")
        this.carreras.imagen='@/assets/carreras/'+this.carreras.nombre+'.jpg'
        console.log(this.carreras.nombre)
        axios.get('https://covidhelper-backend.herokuapp.com/frequentQuestion/'+localStorage.getItem("carrer_id") )
        .then(result =>{
          this.myItems=result.data
        })
      },
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    getData() {
      if (this.career == "Derecho") {
        this.slides = ["Derecho", "Derecho", "Derecho", "Derecho", "Derecho"];
      }
      if (this.career == "Psicologia") {
        this.slides = [
          "Psicologia",
          "Psicologia",
          "Psicologia",
          "Psicologia",
        ]
      }
    },
  },
  watch: {
    id: function(val) {
      this.career = val;
      this.getData();
      this.freqQuestion()
    },
  },
  }
</script>










<style lang="scss" scoped>

